import React, { Component } from "react";
import { inject, observer } from "mobx-react";
import classNames from "classnames";
import { validateInput, validateAllInputs } from "../helpers/validator";
import { Link } from "react-router-dom";

import styles from "./styles.sass";

const START_LENGTH = 0;
const MAX_LENGTH = 40;

@inject("store")
@observer
class SignInForm extends Component {
  state = {
    login: "",
    password: "",
    errors: ""
  };

  componentDidMount() {
    document.addEventListener("keypress", this.onKeyDown);
  }

  componentDidUpdate() {
    if (this.props.store.initialStore.isAuthenticated) {
      this.props.history.push("/");
    }
  }

  componentWillUnmount() {
    document.removeEventListener("keypress", this.onKeyDown);
  }

  onKeyDown = event => {
    if (event.keyCode === 13) this.onSubmit();
  };

  onChange = e => {
    const { name, value } = e.target;
    this.setState({ [name]: value.substr(START_LENGTH, MAX_LENGTH) });
    this.setState({ errors: validateInput(name, value) });
    this.props.store.initialStore.serverErrors = "";
  };

  onSubmit = () => {
    const { login, password } = this.state;

    if (validateAllInputs({ login, password }).isValid) {
      this.setState({ login: "", password: "", errors: "" });

      this.props.store.initialStore.registration(
        { login, password },
        this.props.history
      );
    } else {
      this.setState({ errors: validateAllInputs({ login, password }) });
    }
  };

  render() {
    const { login, password } = this.state;
    const { errors = {} } = this.state.errors;
    const {
      initialStore: { isAuthenticated, serverErrors }
    } = this.props.store;

    return (
      <form className={styles.form}>
        <p className="h3 text-center mb-4">Регистрация</p>
        <p className="text-center error-color">
          {serverErrors && serverErrors}
        </p>
        <label className={classNames({ "error-color": errors.login })}>
          Придумайте логин
        </label>
        <input
          onChange={this.onChange}
          value={login}
          type="text"
          className={classNames("form-control", {
            "has-error": errors.login
          })}
          name="login"
        />
        <span className={styles.error}>{errors.login && errors.login}</span>
        <label
          className={classNames("mt-3", { "error-color": errors.password })}
        >
          Придумайте пароль
        </label>
        <input
          onChange={this.onChange}
          value={password}
          type="password"
          className={classNames("form-control", {
            "has-error": errors.password
          })}
          name="password"
        />
        <span className={styles.error}>
          {errors.password && errors.password}
        </span>
        <div className="text-center mt-4 mb-2">
          <button
            onClick={this.onSubmit}
            className={classNames(styles.btn, "btn btn-primary")}
            type="button"
          >
            Вперед
          </button>
        </div>
        <div className={styles.links}>
          <Link to="/login">Вход</Link>
          <Link to="/forgotpassword">Забыли пароль ?</Link>
        </div>
      </form>
    );
  }
}

export default SignInForm;
