import React from 'react';
import { Route, Link } from 'react-router-dom';
import ReactModal from 'react-modal';
import styles from './styles.sass';

ReactModal.setAppElement('body');

class EverydayTasks extends React.Component {
  state = {
    showModal: false,
  };

  openModal = () => this.setState({ showModal: true });
  closeModal = () => this.setState({ showModal: false });

  render() {
    return (
      <div className={styles.tasks}>
        <a onClick={this.openModal}>Генератор случайных афоризмов</a>
        <Link to="/">Еще какое-то задание...</Link>

        <ReactModal isOpen={this.state.showModal} onRequestClose={this.closeModal} className={styles.reactModal}>
          <div>example</div>
        </ReactModal>
      </div>
    );
  }
}

const ForeWord = () => (
  <div>
    <br />
    <strong>Вводное слово:</strong>
    <br />
    <p>Выполняя ежедневные задания вы...</p>
  </div>
);

const TaskPage = ({ match }) => (
  <div>
    <div className={styles.topNav}>
      <Link to={`${match.url}/everyday`}>
        Ежедневные <span>1</span>
      </Link>
      <Link to={`${match.url}/everyweek`}>
        Еженедельные <span>0</span>
      </Link>
    </div>
    <Route exact path={`${match.url}`} component={ForeWord} />
    <Route path={`${match.url}/everyday`} component={EverydayTasks} />
    <Route path={`${match.url}/everyweek`} component={() => <div>everyweek</div>} />
  </div>
);

export default TaskPage;
