import React from 'react';
import { Link } from 'react-router-dom';

import styles from './styles.sass';

const ProfileDropdown = ({ isActive, exitOfApplication, profileRef }) =>
  <div ref={profileRef} hidden={isActive} className={styles.ProfileDropdownShow}>
    <Link to="/profilesettings">Редактировать</Link>
    <Link to="/helps">Помощь</Link>
    <Link className={styles.exitLink} onClick={exitOfApplication} to="/login">Выйти</Link>
  </div>

export default ProfileDropdown;
