import React from 'react';
import { inject, observer } from 'mobx-react';
import Button from '../../components/common/Button';
import api from '../../actions/api';
import { isObject } from 'lodash';

import styles from './styles.sass';

const defaultImg = require('../../assets/img/defaultPhoto.png');

@inject('store')
@observer
class ProfileSettings extends React.Component {
  constructor(props) {
    super(props);

    this.imgRef = React.createRef();
    this.state = {
      file: null,
      userData: null,
      example: ''
    }
  }

  pickFile = (e) => {
    const pickedFile = e.target.files[0];
    const reader = new FileReader();

    reader.onload = (e) => {
      this.imgRef.current.setAttribute('src', e.target.result);
    }
    reader.readAsDataURL(pickedFile);

    this.setState({ file: e.target.files[0] });
  }

  changeUserProfile = () => {

    if (this.state.file) {
      const formData = new FormData();
      formData.append('file', this.state.file);
      api('change-user-profile', 'POST', formData)
        .then(res => {


          this.props.store.initialStore.userData.avatarImg = `data:image/jpeg;base64, ${res.data}`;
        })
    }

  }

  render() {
    const isPickFile = !isObject(this.state.file);

    return (
      <div className={styles.wrapper}>
        <div className={styles.wrapperPhoto}>
          <div className={styles.imgContainer}>
            <img
              ref={this.imgRef}
              src={defaultImg}
              alt="avatar"
            />
          </div>
          <input
            onChange={this.pickFile}
            type="file"
            accept=".jpg, .jpeg, .png" />
        </div>

        {/* need create common input component */}
        <div className={styles.wrapperBlock}>
          <p>Статус:</p>
          <p>Наставник</p>
        </div>
        <div className={styles.wrapperBlock}>
          <p>Email:</p>
          <p>your@gmail.com</p>
        </div>
        <div className={styles.wrapperBlock}>
          <p>Имя:</p>
          <input value="Имя" className="form-control" />
        </div>
        <div className={styles.wrapperBlock}>
          <p>Город:</p>
          <input value="" className="form-control" />
        </div>
        <div className={styles.wrapperBlock}>
          <p>Деятельность:</p>
          <input value="" className="form-control" />
        </div>
        <div className={styles.wrapperBlock}>
          <p>Интересы:</p>
          <input value="" className="form-control" />
        </div>
        <div className={styles.wrapperBlock}>
          <p>О себе:</p>
          <input value="" className="form-control" />
        </div>

        <div className={styles.wrapperButton}>
          <Button
            disabled={isPickFile}
            text="Сохранить"
            onClick={this.changeUserProfile}
          />
          <Button
            disabled={isPickFile}
            text="Отмена"
            onClick={this.changeUserProfile}
            classN={styles.cancelButton}
          />
        </div>
      </div>
    )
  }
}


export default ProfileSettings;
