import React from 'react';
import { render } from 'react-dom';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Provider } from 'mobx-react';

import SignInForm from './components/Forms/SignInForm';
import SignUpForm from './components/Forms/SignUpForm';
import InDevelopment from './components/common/InDevelopment';

import store from './store';
import { getToken } from './helpers/token';
import setAuthorizationToken from './helpers/setAuthorizationToken';
import App from './App';
import Auth from './Auth';

if (getToken()) {
  setAuthorizationToken(getToken());
  store.initialStore.authentication();
}

render(
  <Provider store={store}>
    <Router>
      <Switch>
        <Route path="/admin/login" component={SignInForm} />
        <Route path="/admin" render={history => (!getToken() ? Auth(history) : <App title="title" />)} />
      </Switch>
    </Router>
  </Provider>,
  document.getElementById('app'),
);

if (module.hot) {
  module.hot.accept();
}
