import React from 'react';
import { inject, observer } from 'mobx-react';
import { withRouter, Link } from 'react-router-dom';

import ProfileDropdown from './ProfileDropdown';

import styles from './styles.sass';

const defaultImg = require('../../assets/img/defaultPhoto.png');
const logoImg = require('../../assets/img/logo.png');

@inject('store')
@observer
export class TopPanel extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isActive: true,
    }
    this.profileRef = React.createRef();
  }

  componentDidMount() {
    const getClass = this.profileRef.current.getAttribute('class');

    window.addEventListener('click', (e) => {
      if (!e.target.closest(`.${getClass}`) && !e.target.closest('.fa-chevron-down')) {
        this.closeProfileBlock();
      }
    });
  }

  openProfileBlock = (e) => this.setState({ isActive: !this.state.isActive })

  closeProfileBlock = () => this.setState({ isActive: true })

  render() {
    const { exitOfApplication, userData } = this.props.store.initialStore;


    return (
      <nav className={styles.nav}>
        {userData.avatarUrl}
        <div className={styles.logo}>
          <img src={logoImg} alt="" />
        </div>

        <div className={styles.navigation}>
          <Link to="/">Главная</Link>
          <Link to="/diarysuccess">Дневник успеха</Link>
          <Link to="/tasks">Задания</Link>
          {/* <Link to="/articles">Статьи</Link>
          <Link to="/targets">Цели</Link> */}
          <Link to="/conversation">Беседка</Link>
          <Link to="/finance">Финансы</Link>
          {/* <Link to="/webinarroom">Вебинарная комната</Link> */}
        </div>

        <div className={styles.avatarContainer}>
          <p>{userData.name || userData.login}</p>
          <div className={styles.imgContainer}>
            <img src={userData.avatarImg || defaultImg} alt="avatar" />
          </div>

          <ProfileDropdown
            profileRef={this.profileRef}
            isActive={this.state.isActive}
            history={this.props.history}
            exitOfApplication={exitOfApplication}
          />

          <i onClick={this.openProfileBlock} className="fa fa-chevron-down" aria-hidden="true" />
        </div>
      </nav>
    );
  }
}

export default withRouter(TopPanel);
