import React from 'react';
import { inject, observer } from 'mobx-react';
import { Route, withRouter } from 'react-router-dom';
import TopPanel from './components/TopPanel';
import Footer from './components/Footer';

/* Pages */
import MainPage from './pages/MainPage';
import TaskPage from './pages/TaskPage';
import TargetsPage from './pages/TargetsPage';
import FinancesPage from './pages/FinancesPage';
import ArticlesPage from './pages/ArticlesPage';
import ConversationPage from './pages/ConversationPage';
import WebinarroomPage from './pages/WebinarroomPage';
import ProfileSettings from './pages/ProfileSettings';

import Loader from './components/common/Loader';

@inject('store')
@observer
export class App extends React.Component {
  renderApp = ({ pathname }, totalAmount, totalExpense) => (
    <div>
      <TopPanel />
      <div className="wrapper wrapper-main">
        <Route exact path="/" component={MainPage} />
        <Route path="/tasks" component={TaskPage} />
        <Route path="/articles" component={ArticlesPage} />
        <Route path="/targets" component={TargetsPage} />
        <Route path="/conversation" component={ConversationPage} />
        <Route path="/finance" component={FinancesPage} />
        <Route path="/webinarroom" component={WebinarroomPage} />
        <Route path="/profilesettings" component={ProfileSettings} />
      </div>
      <Footer />
    </div>
  );

  render() {
    const { isAuthenticated, isLoader, totalAmount, totalExpense } = this.props.store.initialStore;
    return (
      <div>
        {isLoader && <Loader />}
        {isAuthenticated && this.renderApp(this.props.location, totalAmount, totalExpense)}
      </div>
    );
  }
}

export default withRouter(App);
