import { observable } from 'mobx';
import { setToken, removeToken } from '../helpers/token';
import setAuthorizationToken from '../helpers/setAuthorizationToken';
import api from '../actions/api';

class InitialStore {
  constructor(rootStore) {
    this.rootStore = rootStore;
  }

  @observable isLoader = true;

  @observable isAuthenticated = false;

  @observable serverErrors = '';

  @observable userData = {};

  authentication() {
    api('authentication', 'POST')
      .then(res => {
        if (res.status === 200) {
          this.isAuthenticated = true;
          this.isLoader = false;
          this.userData = res.data;
        }
      })
      .catch(reject => console.error(reject));
  }

  login(data, history) {
    this.isLoader = true;
    api('authorization', 'POST', data)
      .then(res => {
        if (res.data.error) {
          this.isLoader = false;
          this.serverErrors = res.data.error;
          return;
        }

        setToken(res.data);
        setAuthorizationToken(res.data);
        this.isAuthenticated = true;
        this.isLoader = false;
        history.push('/');
      })
      .catch(reject => console.error(reject));
  }

  registration(data, history) {
    this.isLoader = true;
    api('registration', 'POST', data)
      .then(res => {
        console.log(res.data);

        if (res.data.error) {
          this.isLoader = false;
          this.serverErrors = res.data.error;
          return;
        }

        setToken(res.data);
        setAuthorizationToken(res.data);
        this.isAuthenticated = true;
        this.isLoader = false;
        history.push('/');
      })
      .catch(reject => console.error(reject));
  }

  exitOfApplication = () => {
    removeToken();
    setAuthorizationToken(false);
    this.isAuthenticated = false;
  };
}

export default InitialStore;
