import React from 'react';
import { inject, observer } from 'mobx-react';
import { Route } from 'react-router-dom';
import ReactModal from 'react-modal';
import classNames from 'classnames';

import TotalAmount from '../../components/TotalAmount';
import CostsList from '../../components/CostsList';

import styles from './styles.sass';

@inject('store')
@observer
class FinancesPage extends React.Component {
  state = {
    showModal: false,
  };

  componentDidMount() {
    this.props.store.pages.financePageFetchData();
  }

  replenishModal = () => {
    return (
      <ReactModal isOpen={this.state.showModal} onRequestClose={this.closeModal} className={styles.reactModal}>
        <div className={styles.modalFormWrapper}>
          <form className={styles.modalForm}>
            <h2>Сумма</h2>
            <i onClick={this.closeModal} className={classNames('fa fa-times', styles.faTimes)} aria-hidden="true" />
            <input type="text" required />
            <button type="submit" className="btn btn-success">
              Пополнить
            </button>
          </form>
        </div>
      </ReactModal>
    );
  };

  expenseModal = () => {
    <ReactModal isOpen={this.state.showModal} onRequestClose={this.closeModal} className={styles.reactModal}>
      <div className={styles.modalFormWrapper}>
        <form className={styles.modalForm}>
          <i onClick={this.openModal} className={classNames('fa fa-times', styles.faTimes)} aria-hidden="true" />
          <div className="food">
            <p>Еда:</p>
            <input name="food" type="text" />
          </div>
          <div className="household">
            <p>Бытовые:</p>
            <input name="household" type="text" />
          </div>
          <div className="travel">
            <p>Проезд:</p>
            <input name="travel" type="text" />
          </div>
          <div className="clothes">
            <p>Одежда:</p>
            <input name="clothes" type="text" />
          </div>
          <div className="rent">
            <p>Кварплата:</p>
            <input name="rent" type="text" />
          </div>
          <div className="mobile">
            <p>Мобильные:</p>
            <input name="mobile" className="mobile" type="text" />
          </div>

          <button type="submit" className="btn btn-success">
            Списать
          </button>
        </form>
      </div>
    </ReactModal>;
  };

  openModal = () => this.setState({ showModal: true });
  closeModal = () => this.setState({ showModal: false });

  render() {
    const { totalAmount, expenseScope } = this.props.store.pages;
    const { match } = this.props;

    return (
      <div>
        <TotalAmount openModal={this.openModal} match={match} totalAmount={totalAmount} />
        <CostsList expenseScope={expenseScope} />

        <Route path={`${match.url}/replenish`} component={this.replenishModal} />
        <Route path={`${match.url}/expense`} component={this.replenishModal} />
      </div>
    );
  }
}

export default FinancesPage;
