import React from 'react';
import { Link } from 'react-router-dom';
import styles from './styles.sass';

const TotalAmount = ({ totalAmount, match, openModal }) => (
  <div>
    <div className={styles.totalAmount}>
      <p>
        Баланс: <strong>{totalAmount}</strong> руб.
      </p>
    </div>

    <div className={styles.buttons}>
      <Link to={`${match.url}/replenish`}>
        <input onClick={openModal} type="button" className="btn btn-success" value="Пополнить" />
      </Link>

      <Link to={`${match.url}/expense`}>
        <input type="button" className="btn btn-success" value="Списать" />
      </Link>
    </div>
  </div>
);

export default TotalAmount;
