import React from 'react';
import className from 'classnames';

const Button = ({
  text = 'defaultText',
  onClick,
  disabled = false,
  styles,
  classN = ''
}) =>
  <div>
    <input
      disabled={disabled}
      type="button"
      className={className(classN, "btn btn-success")}
      onClick={onClick}
      value={text}
      style={styles}
    />
  </div>

export default Button;
